.inputBox{
    margin: 20px auto;
    padding: 5px;
    height: 100px;
    width: 600px;
    background-color: rgb(55, 255, 255);
    text-align: center;
    font-family: sans-serif;
}


.inputBox:hover{
    height: 150px;
    width: 800px;
    
}
#enteredTask{
    right: 60px;
    width: 250px;
    position: relative;
    top: 33px;
    border-radius: 5px;
    height: 33.3px;
    text-align: center;
    font-weight: 600;
}

#addButtom{
    height: 2.3rem;
    border-radius: 5px;
    text-align: center;
    width: 6rem;
    font-weight: 600;
    position: relative;
    top: 33px;
    left: 60px;
}

